<template>
  <div class="text-center">
    <v-dialog v-model="dialog" width="800">
      <v-card>
        <v-card-title class="text-h5 primary white--text">
          Cambio de estado
        </v-card-title>

        <v-card-text>
          <v-switch
            v-model="primera_vez"
            :label="`Primera fase: ${primera_vez.toString()}`"
            @change="updatePrimeraVez"
          ></v-switch>
          <v-switch
            v-model="cierre"
            :label="`Cerrar registro: ${cierre.toString()}`"
            @change="updatedisableAllRegister"
          ></v-switch>
          <vue-editor v-model="content" :editorToolbar="customToolbar"></vue-editor>
          <v-btn class="mt-2"
          :disabled="valid"
            color="primary"
            x-large
            width="250"
            @click="guardarMensaje"
          >
            Guardar
          </v-btn>
          <div class="mt-2 mb-2" v-html="content"></div>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false">
            cerrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import axios from "axios";
  import { mapMutations } from "vuex";
  import { VueEditor } from 'vue2-editor'
  import { apiURL } from "../util/api";
  import eventBus from "./../event-bus";
  export default {
    components: {
      VueEditor
    },
    data() {
      return {
        valid: false,
        loading: false,
        dialog: false,
        primera_vez: false,
        cierre: false,
        content: '',
        customToolbar: [
        ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
        ['blockquote', 'code-block'],

        [{ 'header': 1 }, { 'header': 2 }],               // custom button values
        [{ 'list': 'ordered'}, { 'list': 'bullet' }],
        [{ 'script': 'sub'}, { 'script': 'super' }],      // superscript/subscript
        [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
        [{ 'direction': 'rtl' }],                         // text direction

        [{ 'size': ['small', false, 'large', 'huge'] }],  // custom dropdown
        [{ 'header': [1, 2, 3, 4, 5, 6, false] }],

        [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
        [{ 'font': [] }],
        [{ 'align': [] }],

        ['clean']  
        ]
      };
},
methods: {
  ...mapMutations(["ocultarLoad", "mostrarLoad"]),
  openModal(data) {
    this.dialog = data.dialog;
  },
  async getPrimeraVez() {
    this.loading = true;
    let first = await axios.get(`${apiURL}getFirst`);
    this.loading = false;
      // console.log(first.data.data[0][0].primera_vez)
      this.primera_vez = first.data.data[0][0].primera_vez;
    },
    async updatePrimeraVez(){
      this.loading = true;
      console.log(this.primera_vez)
      await axios.put(`${apiURL}updateFirst`, {
        id: "6199887d45ccd4e8fe18e339",
        primera_vez: this.primera_vez
      });
      this.loading = false;
      // console.log(first.data.data[0][0].primera_vez)
    },
    // Metodos para cerrar los registros
    async getdisableAllRegister() {
    this.loading = true;
    let cierre = await axios.get(`${apiURL}getdisableAllRegister`);
    this.loading = false;
      //console.log(cierre.data.data[0][0].disableRegister)
      this.cierre = cierre.data.data[0][0].disableRegister;
    },
    async updatedisableAllRegister(){
      this.loading = true;
      console.log(this.cierre)
      await axios.put(`${apiURL}updatedisableAllRegister`, {
        id: "61a8e326b2482b32aa2fcbf3",
        disableRegister: this.cierre
      });
      this.loading = false;
      this.$session.set('cierre', this.cierre);
      // console.log(first.data.data[0][0].primera_vez)
    },
    // Funciones para el mensaje de pantalla
    async getMensaje() {
      this.loading = true;
    let texto = await axios.get(`${apiURL}getText`);
    this.loading = false;
      // console.log(texto.data.data[0].text)
      this.content = texto.data.data[0].text;
    },
    //Modificar mensaje
    async guardarMensaje() {
      this.valid = true
      this.mostrarLoad({ titulo: "Enviando datos, por favor espere" });
      //console.log(this.cierre)
      await axios.put(`${apiURL}updateText`, {
        id: "61a96f03830e6f3fe650cf48",
        text: this.content
      });
      //console.log(respueta.data.data)
      this.ocultarLoad();
      this.valid = false
    }
  },
  mounted() {
    eventBus.$on("ModalCambioFase", data => {
      this.openModal(data); // {name:'foo'}
    });
    this.getdisableAllRegister()
    this.getPrimeraVez();
    this.getMensaje();
  }
};
</script>