<template>
  <v-container class="leblanc mb-6">
    <v-row  class="text-center">
      <v-col cols="12">
        <br />
        <v-img
        :src="require('../assets/logo_FEU_gray@2x.png')"
        class="my-3"
        contain
        height="200"
        />

        <br />
        <br />
        <div v-html="mensajeHTML"></div>
        
        <!-- El periodo inicia del 10 al<br />
        11 de noviembre, te invitamos a visitarnos <br />
        en ese periodo para que puedas<br />
        generar tu registro<br /> -->
        <br />
        <br />
        <br />
      </v-col>

  </v-row>
</v-container>
</template>

<script>
  import axios from "axios";
  import { apiURL } from "../util/api";
export default {
  name: 'Noactiva',
  data: () => ({
    mensajeHTML: ``
  }),
  methods: {
    async getMensaje() {
      this.loading = true;
    let texto = await axios.get(`${apiURL}getText`);
    this.loading = false;
      // console.log(texto.data.data[0].text)
      this.mensajeHTML = texto.data.data[0].text;
    },
  },
  mounted(){
    this.getMensaje();
  },
  created() {
    this.$session.clear()
  }
};
</script>
